#gradient {
    font-family: 'Inter', sans-serif ;
    font-style: normal;
    background: linear-gradient(to right, #C76BFF, #ffb444, #ffb444, #ffb444, #ffb444, #ffb444, #ffb444);
    color: transparent;
    -webkit-background-clip: text;
            background-clip: text;
}

#text {
    font-family: 'Roboto';
    font-style: normal;
}

.flow-text {
    display: flex;
    white-space: nowrap;
    overflow: hidden;
    transition: 0.3s;
    font-size: 96px;
    letter-spacing: 0.1em;
    font-weight:bold;
    color: #363636;
}
.flow-wrap {
  -webkit-animation: textLoop 10s linear infinite;
          animation: textLoop 10s linear infinite;
  padding-right: 1.4881vw;
}

@-webkit-keyframes textLoop {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-100%, 0, 0);
  }
}

@keyframes textLoop {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-100%, 0, 0);
  }
}

